// 配置路由以下3个必须引入
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';

// 登录
import { LoginComponent } from './login/login.component';
// 首页
import { HomeComponent } from './home/home.component';
// 主页
import { IndexComponent } from './home/index/index.component';
// 题库
import { TopicComponent } from './home/topic/topic.component';
import { NavComponent } from './home/topic/nav/nav.component';
import { Css3Component } from './home/topic/css3/css3.component';
import { Html5Component } from './home/topic/html5/html5.component';
import { JavascriptComponent } from './home/topic/javascript/javascript.component';
import { Angular7Component } from './home/topic/angular7/angular7.component';
import { TypescriptComponent } from './home/topic/typescript/typescript.component';


export const routes: Routes = [
  //默认首页路径
  { path:'',redirectTo:'login',pathMatch:'full' },
  // 登录页
  { path:'login',component:LoginComponent },
  { path:'home',component:HomeComponent,children:[
    { path:'',redirectTo:'index',pathMatch:'full' },
    { path:'index',component:IndexComponent },
    { path:'topic',component:TopicComponent,children:[
      { path:'',redirectTo:'nav',pathMatch:'full' },
      { path:'nav',component:NavComponent },
      { path:'css3',component:Css3Component },
      { path:'html5',component:Html5Component },
      { path:'javascript',component:JavascriptComponent },
      { path:'angular7',component:Angular7Component },
      { path:'typescript',component:TypescriptComponent }
    ] }
  ] }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class AppRoutingModule { }
