//变化检测ChangeDetectorRef
import { Component, OnInit, Output ,Input,ChangeDetectorRef } from '@angular/core';
// ng-zorro组件
import { NzMessageService } from 'ng-zorro-antd';
// 输出字符串中的标签
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';

@Component({
  selector: 'app-publics',
  templateUrl: './publics.component.html',
  styleUrls: ['./publics.component.scss']
})
export class PublicsComponent implements OnInit {

  constructor(
    private msgService:NzMessageService,
    private Ref:ChangeDetectorRef
  ) { }
  
  // 用来输出带标签的字符串
  //接收外部传来的数据
  @Input() title:SafeHtml;//标题，可以输出字符串里的标签
  @Input() topicList:any;

  // 当前展示的题
  public topics:any = [];
  //总题目数
  public count:number = null;
  // 对错数 
  public checkeds:number = 0;
  // 当前第几题
  public current:number = 1;
  // 总完成数
  public complete:number = 0;
  // 当前时间
  private timer:any = null;
  public date = new Date();
  // 倒计时
  private tDown:any = null;//倒计时
  public newDate = '05分00秒';//要渲染的自段
  private second = 300;//每题的时间单位秒 5分钟
  // 完成后的模态框
  public isVisible = false;
  // 答案
  public answer:SafeHtml;

  ngOnInit() {
    // 乱序输出
    this.topicList.sort((a,b)=>{
      return Math.random() > 0.5 ? -1 : 1;
    })
    this.topics = this.topicList[0];
    this.answer = this.topics.answer;
    this.count = this.topicList.length;
    // 当前时间
    this.timer = setInterval(()=>{
      this.date = new Date();
    },1000)
    // 倒计时
    this.timeDown(this.second);
  }
  ngOnDestroy(){
    clearInterval(this.timer);
    clearInterval(this.tDown);
  }

  // 倒计时
  timeDown(time){
    this.tDown = setInterval(()=>{
      time--;
      const m = Math.floor(time / 60);
      const s = Math.floor(time % 60);
      this.newDate = ( m < 10 ? '0' + m : m )+'分'+( s < 10 ? '0' + s : s )+'秒';
      if(time == 0){
        this.topics.checked = '2';
        this.turning('text');
        this.msgService.create('warning','超出时间，请进行下一题！');
      }
    },1000)
  }
  // 选择对错
  topicChange(event,item){
    item.checked = event;
    this.complete += 1;
    if(event=='1'){
      this.checkeds += 1;
    }
    if(this.complete == this.topicList.length){
      this.isVisible = true;
    }
  }
  // 关闭提示框
  handleOk(){
    this.isVisible = false;
  }
  // 上一题/下一题
  turning(val){
    if(val == 'text'){//下一题
      if(this.topics.checked == '0'){
        this.msgService.create('warning','请完成当前题后进入下一题！');
        return;
      }
      this.topics = this.topicList[this.current];
      this.answer = this.topics.answer;
      this.current += 1;
      clearInterval(this.tDown);
      this.timeDown(this.second);
    }else{
      this.current -= 1;
      this.topics = this.topicList[this.current];
      this.answer = this.topics.answer;
      clearInterval(this.tDown);
      this.timeDown(this.second);
    }
    this.Ref.detectChanges();
  }
  // 查看答案
  seeLook(){
    this.topics.look = true;
  }
}
